@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&family=Sixtyfour&display=swap");

/*
font-family: 'Roboto Mono', monospace;
font-family: 'Sixtyfour', sans-serif;
*/

* {
  background-color: black;
}

a {
  text-decoration: none;
}

.App {
  text-align: center;
  margin: 0px;
  padding: 0px;
  font-size: 28px;
}

body {
  margin: 0;
  /*font-family: "Fira Sans", sans-serif;*/
  /*colors*/
  --color1: rgb(62, 208, 18);
  --color2: rgb(0, 0, 0);
  --color3: orange;
  --color4: rgb(168, 25, 152);

  --text-focus: rgb(255, 170, 12);
  --text-secondary: white;

  /* background: var(--color1); */
  animation: bgcolor infinite 10s ease-in-out;
  color: var(--color2);
}
