* {
  cursor: none;
}

.HHMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  font-family: "Sixtyfour", sans-serif;
}

.HHCont1 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: start;
  width: 50%;
}

.HHMain h3 {
  font-size: 38px;
  margin: 10px;
  color: var(--color1);
}

.HHMain h4 {
  font-size: 18px;
  color: var(--color4);
  margin-top: 70px;
}

.Cursor {
  background-color: var(--color1);
  height: 32px;
  width: 32px;
  border-radius: 50%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
}

.HHElement1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color2);
  height: 120px;
  width: 300px;
  border-radius: 50%;
  margin-top: 100px;
  color: var(--color2);

  /* border: solid red 1px; */
}

.HHElement1 p {
  font-size: 40px;
  transition: 0.5s;
  margin: 0px;
  padding: 0px;
}

.HHElement1:hover {
  color: var(--color3);
}

.Boing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color3);
  height: 10px;
  width: 10px;
  border-radius: 50%;
  animation: boing 1s linear infinite;
}

@keyframes boing {
  0% {
    transform: translateY(0px) translateX(0px);
  }
  20% {
    transform: translateY(8px) translateX(6px);
  }
  25% {
    transform: translateY(16px) translateX(7px);
  }
  50% {
    transform: translateY(0px) translateX(0px);
  }
  70% {
    transform: translateY(8px) translateX(-6px);
  }
  75% {
    transform: translateY(16px) translateX(-7px);
  }
}
